*{
    margin: 0;
    padding: 0;
}

.main-navigation{
    background-color: rgba(10, 14, 29);
    box-shadow: -1rem 2rem 3rem #000;
}

.nav-list-item{
    font-weight: 800;
    display: inline-block;
    line-height: 50px;
    margin-left: 15px;
    margin-right: 60px;
    margin-top: 10px;
    padding-bottom: 2%;
}

.nav-link{
    color: orange;
    text-decoration: none;
}

.nav-link:hover{
    color: #F61D1D;
    font-size: large;
    text-decoration: underline;
    text-shadow: -1rem 0 3rem rgb(114, 223, 12);
}

nav.main-navigation ul.nav-list {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    text-align: right;
}


button{
    color:orange;
    background-color: rgb(31, 78, 26);
    padding: 8px;
    border-style:groove;
    border-radius: 10px;
    font-size: larger;
    font-weight: 800;
    border-color: #000;
    border-radius: 10px;
}

.btn-1:hover{
    color:#F61D1D;
    font-size: x-large;
}

@keyframes FadeIn {
        0% {
    opacity: 0;
    -webkit-transition-duration: 0.8s;
    transition-duration: 0.8s;
    -webkit-transform: translateY(-10px);
    -ms-transform: translateY(-10px);
    transform: translateY(-10px);
    }


 100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
    pointer-events: auto;
    transition: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

 .nav-list li {
     animation: FadeIn 1s cubic-bezier(0.65, 0.05, 0.36, 1);
     animation-fill-mode: both;
}

.nav-list li:nth-child(1) {
    animation-delay: .3s;
}

.nav-list li:nth-child(2) {
    animation-delay: .6s;
}

.nav-list li:nth-child(3) {
    animation-delay: .9s;
}

.nav-list li:nth-child(4) {
    animation-delay: 1.2s;
}

.nav-list li:nth-child(5) {
    animation-delay: 1.5s;
}

    /* Animation */

@keyframes fadeInUp {
        from {
    transform: translate3d(0, 40px, 0)
    }
        to {
    transform: translate3d(0, 0, 0);
    opacity: 1
    }
}
    @-webkit-keyframes fadeInUp {
        from {
    transform: translate3d(0, 40px, 0)
    }
        to {
    transform: translate3d(0, 0, 0);
    opacity: 1
    }
}

.animated {
    animation-duration: 1s;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
    opacity: 0
}

.fadeInUp {
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
}